.checkbox,
.radio {
	display: inline-block;
	vertical-align: top;
	position: relative;
	min-height: 20px;
	min-width: 20px;
	padding-left: 20px;
	font-size: 0;
	cursor: pointer;
	@include nosel;
	input {
		display: none; }
	&__icon {
		position: absolute;
		top: 0;
		left: 0;
		width: 20px;
		height: 20px;
		border: solid 1px #c9c9c9; }
	&__label {
		display: inline-block;
		vertical-align: top;
		padding-top: 3px;
		margin-left: 10px;
		@include f(14px, 400, 16px); } }
.checkbox {
	input {
		&:checked ~ .checkbox__icon .checkbox__icon-in {
			opacity: 1;
			transform: translateY(5px); } }
	&__icon {
		border-radius: 4px; }
	&__icon-in {
		position: absolute;
		opacity: 0;
		left: 4px;
		top: 1;
		font-size: 8px;
		transition: opacity .3s, transform .3s; } }
.radio {
	input {
		&:checked ~ .radio__icon:before {
			opacity: 1;
			transform: scale(1); } }
	&__icon {
		border-radius: 50%;
		&:before {
			content: "";
			position: absolute;
			left: 5px;
			top: 5px;
			right: 5px;
			bottom: 5px;
			border-radius: 50%;
			opacity: 0;
			background: $blue;
			transform: scale(.5);
			transition: opacity .3s, transform .3s; } } }

