.table__title {
  color: #007AFF;
  font-size: 16px;
  line-height: 1;
}

.table__meta {
  font-size: 14px;
  line-height: 1;
}

.table__th {
  padding-bottom: 3px;
  color: #a5afba;
  font-size: 12px;
  line-height: 1;
}

.table__btn {
  position: absolute;
  right: -14px;
  top: 50%;
  transform: translateY(-50%);
}

@media only screen and (max-width: 567px) {
  .table__btn {
    top: 0;
    transform: translate3d(0, 0, 0);
  }
}

.section {
  margin-bottom: 40px;
}

@media only screen and (max-width: 767px) {
  .section {
    margin-bottom: 20px;
  }
}

.section.is-editing .section__editing {
  display: inline-block;
}

.section.is-editing .section__selection {
  display: none;
}

.section.is-editing .static-table__tr {
  padding-left: 35px;
}

@media only screen and (max-width: 767px) {
  .section.is-editing .static-table__tr {
    padding-left: 0;
    padding-right: 30px;
  }
}

.section.is-editing .static-table__checkbox {
  display: block;
}

.section.is-selected .section__title {
  display: none;
}

.section.is-selected .section__selected {
  display: inline-block;
}

.section__header {
  position: relative;
  margin-bottom: 10px;
  min-height: 35px;
}

.section__header:after {
  content: " ";
  display: table;
  clear: both;
}

.section__title {
  display: inline-block;
  margin-top: 7px;
}

@media only screen and (max-width: 567px) {
  .section__title {
    margin: 0 0 10px;
  }
}

.section__selected {
  display: none;
  margin-top: 7px;
  font-size: 15px;
  line-height: 20px;
  color: #a5afba;
}

@media only screen and (max-width: 567px) {
  .section__selected {
    margin: 0 0 10px;
  }
}

.section__btns {
  float: right;
  margin: -5px;
  font-size: 0;
}

@media only screen and (max-width: 567px) {
  .section__btns {
    float: none;
  }
}

.section__editing {
  display: none;
}

.section__btn {
  margin: 5px;
}

.partition {
  margin-bottom: 30px;
}

@media only screen and (max-width: 567px) {
  .partition {
    margin-bottom: 10px;
  }
}

.partition_sm {
  max-width: 455px;
  margin-bottom: 40px;
}

@media only screen and (max-width: 567px) {
  .partition_sm {
    max-width: 100%;
  }
}

.partition__header {
  margin-bottom: -10px;
}

.partition__header:after {
  content: " ";
  display: table;
  clear: both;
}

@media only screen and (max-width: 567px) {
  .partition__header {
    margin-bottom: 0;
  }
}

.partition__middle {
  padding-top: 15px;
}

.partition__middle:after {
  content: " ";
  display: table;
  clear: both;
}

.partition__title {
  display: inline-block;
  margin: 0 10px 10px 0;
}

.partition__checkbox {
  float: right;
}

.partition__radios {
  display: inline-block;
  vertical-align: top;
  padding: 5px 0;
  font-size: 0;
  margin: 0 -15px 10px;
}

@media only screen and (max-width: 455px) {
  .partition__radios {
    display: block;
    text-align: center;
  }
}

.partition__radio {
  margin: 0 15px;
}

.partition__grid {
  display: inline-block;
  width: 100%;
}

@media only screen and (max-width: 455px) {
  .partition__btn {
    text-align: center;
    margin-bottom: 10px;
  }
}

.partition__btn_right {
  float: right;
}

@media only screen and (max-width: 455px) {
  .partition__btn_right {
    float: none;
  }
}
